import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Button from "@material-ui/core/Button";
import uniqid from 'uniqid'


export default function ParcelTokenGeneration() {
  const [open, setOpen] = React.useState(false);
  const [uniqToken, setUniqToken] = React.useState('');
  const classes = useStyles();

  const handleClickOpen = () => {
     setUniqToken(uniqid.time());
     setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
    
      <Button variant="contained" className={classes.btn} color="primary"  onClick={handleClickOpen}>
       Parcel Customers
      </Button>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title" className="title-center" color={'primary'} >  {uniqToken}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
           Your toke is <strong>  {uniqToken} </strong>
          Please click on print to get your token printed.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          
          <Button onClick={handleClose} color="primary" autoFocus>
            Print
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
const useStyles = makeStyles(theme => ({
    root: {
      flexGrow: 1
    },
    paper: {
      padding: theme.spacing(2),
      textAlign: "center",
      color: theme.palette.text.secondary
    },
    topMargin: {
      marginTop: "10%"
    },
    btn: {
      margin: "0 auto",
      display: "block",
      width: "70%",
      backgroundImage:
        " linear-gradient(to right, #4CB8C4 0%, #3CD3AD 51%, #4CB8C4 100%)",
      fontWeight: 800,
      lineHeight: 3.75,
      color: "black",
      "&:hover": {
        backgroundPosition: "right center"
      }
    }
  }));