

export default function DiscountCalcUtil(values) {
debugger

    console.log('Discount calc', values);
debugger
    let guestCount = values.length;  //2
    let dobCount = 0; // 20 
    let doaCount = 0; // 20
    let dobDiscount = 0;
    let doaDiscount = 0;

    if (guestCount) {
        values.forEach(guest => {

            if (guest.dob) dobCount++
            if (guest.doa) doaCount++
            
        });

        dobDiscount = ((dobCount * 10) / (guestCount * 10)) * 10;
        doaDiscount = (doaCount * 5) / (guestCount * 5 ) * 5;
        console.log(dobDiscount + doaDiscount + "%");
        return (dobDiscount + doaDiscount);

    }

}