import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import ListItem from "@material-ui/core/ListItem";
import List from "@material-ui/core/List";
import InputAdornment from "@material-ui/core/InputAdornment";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import CloseIcon from "@material-ui/icons/Close";
import Slide from "@material-ui/core/Slide";
import TextField from "@material-ui/core/TextField";
import AccountCircleOutlinedIcon from "@material-ui/icons/AccountCircleOutlined";
import Container from "@material-ui/core/Container";
import DateFnsUtils from "@date-io/date-fns";
import MailOutlineIcon from "@material-ui/icons/MailOutline";
import CallIcon from "@material-ui/icons/Call";
import AddGuest from "./AddGuest";
import { useForm } from "react-hook-form";
import { Rating } from "@material-ui/lab";
import Box from "@material-ui/core/Box";
import NotificationBar from './NotificationBar';
import AjaxHelper from './helpers/AjaxHelper'


import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker
} from "@material-ui/pickers";

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});
export default function UserDataForm() {
  const { handleSubmit, register, errors, reset } = useForm();
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  const [guestsList, setGuestList] = React.useState([]);

  const updateGuest = guest => {
    // This is how we set array in hooks
    setGuestList([...guestsList, guest]);
  };

  const handleClickOpen = () => {
    setOpen(true);
  };
  const hasMainError = inputName => !!(errors && errors[inputName]);
  const handleClose = () => {
    setOpen(false);
    setGuestList([]);
  };
  const [selectedDate, setSelectedDate] = React.useState(
    new Date("2014-08-18T21:11:54")
  );

  const handleDateChange = date => {
    setSelectedDate(date);
  };
  const onSubmit = values => {
    //   let myRecords = new Array (guestsList);
    //   myRecords.push(values);
      
    console.log(values);
    console.log('guestlist',guestsList);
   AjaxHelper.createPost({
    customerData: values,
    guestData : guestsList

   })
    setOpen(false);
    setGuestList([]);
   
  };

  return (
    <div>
      <Button
        variant="contained"
        className={classes.btn}
        color="primary"
        onClick={handleClickOpen}
      >
        Get Discount
      </Button>

      <Dialog
        fullScreen
        open={open}
        onClose={handleClose}
        TransitionComponent={Transition}
      >
        <form className={classes.root} onSubmit={handleSubmit(onSubmit)}>
          <AppBar className={classes.appBar}>
            <Toolbar>
              <IconButton
                edge="start"
                color="inherit"
                onClick={handleClose}
                aria-label="close"
              >
                <CloseIcon />
              </IconButton>
              <Typography variant="h6" className={classes.title}>
                Cancel
              </Typography>
              <Button autoFocus color="inherit" type="submit">
                Save
              </Button>
            </Toolbar>
          </AppBar>

          <Container maxWidth="md" className="form-container">
            <List>
              <ListItem>
                <TextField
                  fullWidth
                  id="standard-basic"
                  name="custName"
                  inputRef={register({
                    required: "Required"
                  })}
                  error={hasMainError("custName")}
                  helperText="Enter your name"
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <AccountCircleOutlinedIcon color="primary" />
                      </InputAdornment>
                    )
                  }}
                />
              </ListItem>
              <ListItem>
                <TextField
                  fullWidth
                  id="EmailAddress"
                  helperText="Enter email address"
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <MailOutlineIcon color="primary" />
                      </InputAdornment>
                    )
                  }}
                />
              </ListItem>
              <ListItem>
                <TextField
                  fullWidth
                  id="ContactNumber"
                  name="custMobile"
                  inputRef={register({
                    required: "Required"
                  })}
                  error={hasMainError("custMobile")}
                  helperText="Enter your mobiles"
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <CallIcon color="primary" />
                      </InputAdornment>
                    )
                  }}
                />
              </ListItem>

              <ListItem>
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                  <KeyboardDatePicker
                    margin="normal"
                    id="date-picker-dialog"
                    format="MM/dd/yyyy"
                    value={selectedDate}
                    onChange={handleDateChange}
                    name="custDate"
                    inputRef={register({
                      required: "Required"
                    })}
                    error={hasMainError("custDate")}
                    helperText="Birth date or Anniversary"
                    color="primary"
                    KeyboardButtonProps={{
                      "aria-label": "change date"
                    }}
                  />
                </MuiPickersUtilsProvider>
              </ListItem>
            </List>

            <GuestAddedComponent guestListData={guestsList} />
          </Container>
        </form>
        <Container maxWidth="md">
          <AddGuest onStarUpdate={updateGuest} />
        </Container>
      </Dialog>
    
    </div>
  );
}

  

function GuestAddedComponent(guestsList) {
    let notifiactionArea;

    if (guestsList.guestListData.length) notifiactionArea =   <NotificationBar guestCount = {true} />;

  return (
    <div>
      <Box component="fieldset" mb={3} borderColor="transparent">
        <Typography component="legend">Discount earned </Typography>
        <Rating
          name="read-only"
          value={guestsList.guestListData.length}
          readOnly
        />
      </Box>
      {notifiactionArea}
    </div>
  );
}
const useStyles = makeStyles(theme => ({
  appBar: {
    position: "relative",
    backgroundImage:
      " linear-gradient(to right, #4CB8C4 0%, #3CD3AD 51%, #4CB8C4 100%)"
  },
  btn: {
    margin: "0 auto",
    display: "block",
    width: "70%",
    backgroundImage:
      " linear-gradient(to right, #4CB8C4 0%, #3CD3AD 51%, #4CB8C4 100%)",
    fontWeight: 800,
    lineHeight: 3.75,
    color: "black",
    "&:hover": {
      backgroundPosition: "right center"
    }
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1
  },
  cancelBtn: {
    width: "20%",
    marginTop: "5%"
  },
  savebtn: {
    float: "right",
    width: "20%",
    marginTop: "5%"
  }
}));
