import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import ListItem from "@material-ui/core/ListItem";
import List from "@material-ui/core/List";
import InputAdornment from "@material-ui/core/InputAdornment";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import CloseIcon from "@material-ui/icons/Close";
import Slide from "@material-ui/core/Slide";
import TextField from "@material-ui/core/TextField";
import AccountCircleOutlinedIcon from "@material-ui/icons/AccountCircleOutlined";
import Container from "@material-ui/core/Container";
import DateFnsUtils from "@date-io/date-fns";
import MailOutlineIcon from "@material-ui/icons/MailOutline";
import CallIcon from "@material-ui/icons/Call";
import { useForm } from "react-hook-form";

import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker
} from "@material-ui/pickers";


const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function AddGuestForm({onStarUpdate}) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  // const [guestName, guestEmail,guestDate] = React.useState('');

  const { handleSubmit, register, errors, reset } = useForm();

  const hasError = inputName => !!(errors && errors[inputName]);

  const guestHandleClickOpen = () => {
    setOpen(true);
  };

  const guestHandleClose = () => {
    setOpen(false);
  };
  const guestHandleSave = () => {
    console.log("saving guest");
    // GuesthandleSubmit();
  };

  const [sGuestDate, setSelectedGuestDate] = React.useState(
    new Date("2014-08-18T21:11:54")
  );
  const handleGDateChange = date => {
    setSelectedGuestDate(date);
  };

  const onSubmit = values => {
   // console.log(values);
    onStarUpdate(values);
    reset();
    setOpen(false);
  };

  return (
    <div className={classes.guestWrapper}>
      <Button
        variant="contained"
        color="secondary"
        className={classes.addGuestBtn}
        onClick={guestHandleClickOpen}
      >
        Add Guest
      </Button>

      <Dialog
        fullScreen
        open={open}
        onClose={guestHandleClose}
        TransitionComponent={Transition}
      >
        <AppBar className={classes.appBar}>
          <Toolbar>
            <IconButton
              edge="start"
              color="inherit"
              onClick={guestHandleClose}
              aria-label="close"
            >
              <CloseIcon />
            </IconButton>
            <Typography variant="h6" className={classes.title}>
              Back
            </Typography>
            <Button
              type="submit"
              autoFocus
              color="inherit"
              onClick={guestHandleSave}
            >
              Save
            </Button>
          </Toolbar>
        </AppBar>

        <Container maxWidth="md" className="form-container">
          <form className={classes.root} onSubmit={handleSubmit(onSubmit)}>
            <List>
              <ListItem>
                <TextField
                  fullWidth
                  id="standard-basic"
                  name="guestName"
                  inputRef={register({
                    required: "Required"
                  })}
                  error={hasError("guestName")}
                  helperText="Guest name is mandatory"
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <AccountCircleOutlinedIcon color="primary" />
                      </InputAdornment>
                    )
                  }}
                />
              </ListItem>
              <ListItem>
                <TextField
                  fullWidth
                  id="EmailAddress"
                  name="guestEmail"
                  inputRef={register({
                    pattern: {
                      value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i,
                      message: "invalid email address"
                    }
                  })}
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <MailOutlineIcon color="primary" />
                      </InputAdornment>
                    )
                  }}
                  error={hasError("guestEmail")}
                  helperText="Email is mandatory"
                />
              </ListItem>
              <ListItem>
                <TextField
                  fullWidth
                  id="ContactNumber"
                  name="guestContact"
                  type='number'
                  inputRef={register({
                    required: "Required"
                  })}
                  error={hasError("guestContact")}
                  helperText="Contact number is mandatory"
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <CallIcon color="primary" />
                      </InputAdornment>
                    )
                  }}
                />
              </ListItem>

              <ListItem>
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                  <KeyboardDatePicker
                    margin="normal"
                    id="date-picker-dialog"
                    label="Birth date or Anniversary"
                    format="MM/dd/yyyy"
                    name="guestDate"
                     value={sGuestDate}
                    onChange={handleGDateChange}
                    error={hasError("guestDate")}
                    helperText="date is mandatory"
                    color="primary"
                    inputRef={register({
                      required: "Required"
                    })}
                    KeyboardButtonProps={{
                      "aria-label": "change date"
                    }}
                  />
                </MuiPickersUtilsProvider>
              </ListItem>
            </List>
            <Button
              variant="contained"
              color="secondary"
              type="submit"
              className={classes.cancelBtn}
              onClick={reset}
            >
              Cancel
            </Button>
            <Button
              variant="contained"
              color="secondary"
              type="submit"
              className={classes.savebtn}
            >
              Save
            </Button>
          </form>
        </Container>
      </Dialog>
    </div>
  );
}


const useStyles = makeStyles(theme => ({
    appBar: {
      position: "relative",
      backgroundImage:
        " linear-gradient(to right, #4CB8C4 0%, #3CD3AD 51%, #4CB8C4 100%)"
    },
  
    title: {
      marginLeft: theme.spacing(2),
      flex: 1
    },
    cancelBtn: {
      width: "20%",
      marginTop: "5%"
    },
    addGuestBtn:{
        width: "18%"
    },
    savebtn: {
      float: "right",
      width: "20%",
      marginTop: "5%"
    },
    guestWrapper: {
      display: "inline"
    }
  }));