export default function dateCalculations() {
    console.log('Date calculation');
  let today = new Date();
  let localItemObj = { 
      'Token': 0, TDate: new Date().setHours(0,0,0,0)
     };
  let localStr = localStorage.getItem("localItems");

  if (!localStr) {
    localStorage.setItem("localItems", JSON.stringify(localItemObj));
  } else {
      let dateObject = JSON.parse(localStr);
      if ( today.setHours(0,0,0,0) > dateObject.TDate  ) {
        localStorage.setItem("localItems", JSON.stringify(localItemObj));
      }

  }
}
