import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import ListItem from "@material-ui/core/ListItem";
import List from "@material-ui/core/List";
import InputAdornment from "@material-ui/core/InputAdornment";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import CloseIcon from "@material-ui/icons/Close";
import Slide from "@material-ui/core/Slide";
import TextField from "@material-ui/core/TextField";
import AccountCircleOutlinedIcon from "@material-ui/icons/AccountCircleOutlined";
import Container from "@material-ui/core/Container";
import CallIcon from "@material-ui/icons/Call";
import { useForm } from "react-hook-form";
import { Rating } from "@material-ui/lab";
import Box from "@material-ui/core/Box";
import NotificationBar from './NotificationBar';
import SimpleModalComponent from './helpers/SimpleModalComp';

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function NonRegisteredCustomer({customerType}) {
    console.log(customerType);
  const { handleSubmit, register, errors, reset } = useForm();

  const [openSimple, setOpenSimple] = React.useState(false);
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [tokenCount, setTokenCount] = React.useState(0);

  const [guestsList, setGuestList] = React.useState([]);


  const handleClickOpen = () => {
    setOpen(true);
  };
  const closeParentModal = (flag) => {
    setOpen(flag);
    setOpenSimple(false);
  };
  const hasMainError = inputName => !!(errors && errors[inputName]);
  const handleClose = () => {
    setOpen(false);
    setGuestList([]);
  };
  const onSubmit = values => {

    setGuestList(values);
    let retrievedObject = JSON.parse(localStorage.getItem('localItems') || "");
    retrievedObject.Token = retrievedObject.Token + 1;
    setTokenCount(retrievedObject.Token);
    localStorage.setItem('localItems', JSON.stringify(retrievedObject));


   setOpenSimple(true);
   reset();
  };

  return (
    <div>
      <Button
        variant="contained"
        className={classes.btn}
        color="primary"
        onClick={handleClickOpen}
      >
       Non-Registered Customers
      </Button>

      <Dialog
        fullScreen
        open={open}
        onClose={handleClose}
        TransitionComponent={Transition}
      >
        <form className={classes.root} onSubmit={handleSubmit(onSubmit)}>
          <AppBar className={classes.appBar}>
            <Toolbar>
              <IconButton
                edge="start"
                color="inherit"
                onClick={handleClose}
                aria-label="close"
              >
                <CloseIcon />
              </IconButton>
              <Typography variant="h6" className={classes.title}>
                Cancel
              </Typography>
              <Button autoFocus color="inherit" type="submit">
                Save
              </Button>
            </Toolbar>
          </AppBar>

          <Container maxWidth="md" className="form-container">
            <List>
              
            
              <ListItem>
                <TextField
                  fullWidth
                  id="ContactNumber"
                  name="NcustMobile"
                  type="number"
                  inputRef={register({
                    required: "Required"
                  })}
                  error={hasMainError("NcustMobile")}
                  helperText="Enter your mobiles"
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <CallIcon color="primary" />
                      </InputAdornment>
                    )
                  }}
                  onInput={(e)=>{ 
                    e.target.value = Math.max(0, parseInt(e.target.value) ).toString().slice(0,10)
                 }}
                />
              </ListItem>

              <ListItem>
                <TextField
                  fullWidth
                  id="standard-basic"
                  name="guestCount"
                  type="number"
                  inputRef={register({
                    required: "Required"
                  })}
                  error={hasMainError("guestCount")}
                  helperText="Enter guest count"
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <AccountCircleOutlinedIcon color="primary" />
                      </InputAdornment>
                    )
                  }}
                />
              </ListItem>
            </List>

            <Button
              variant="contained"
              color="secondary"
              type="submit"
              className={classes.cancelBtn}
              onClick={handleClose}
            >
              Cancel
            </Button>
            <Button
              variant="contained"
              color="secondary"
              type="submit"
              className={classes.savebtn}
            >
              Save
            </Button>

          </Container>
        </form>
      </Dialog>
      {openSimple &&  <SimpleModalComponent  closeParentModal = {closeParentModal }  token={customerType} guestCount={guestsList} count={tokenCount} /> }
    </div>
  );
}

  

function GuestAddedComponent(guestsList) {
    let notifiactionArea;

    if (guestsList.guestListData.length) notifiactionArea =   <NotificationBar guestCount = {true} />;

  return (
    <div>
      <Box component="fieldset" mb={3} borderColor="transparent">
        <Typography component="legend">Discount earned </Typography>
        <Rating
          name="read-only"
          value={guestsList.guestListData.length}
          readOnly
        />
      </Box>
      {notifiactionArea}
    </div>
  );
}
const useStyles = makeStyles(theme => ({
  appBar: {
    position: "relative",
    backgroundImage:
      " linear-gradient(to right, #4CB8C4 0%, #3CD3AD 51%, #4CB8C4 100%)"
  },
  btn: {
    margin: "0 auto",
    display: "block",
    width: "70%",
    backgroundImage:
      " linear-gradient(to right, #4CB8C4 0%, #3CD3AD 51%, #4CB8C4 100%)",
      fontWeight: 500,
    height: '100px',
    color: "#151c1d",
    marginTop:'8%',
    "&:hover": {
      backgroundPosition: "right center"
    },
 
  },

  title: {
    marginLeft: theme.spacing(2),
    flex: 1
  },
  cancelBtn: {
    width: "20%",
    marginTop: "5%"
  },
  savebtn: {
    float: "right",
    width: "20%",
    marginTop: "5%"
  }
}));
