import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Modal from "@material-ui/core/Modal";
import ArrowRightAltIcon from '@material-ui/icons/ArrowRightAlt';

function rand() {
  return Math.round(Math.random() * 20) - 10;
}

function getModalStyle() {
  const top = 50 + rand();
  const left = 50 + rand();

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`
  };
}

const useStyles = makeStyles(theme => ({
  paper: {
    position: "absolute",
    width: 400,
    backgroundColor: theme.palette.background.paper,
    border: "2px solid #000",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3)
  }
}));

export default function SimpleModalComp(props) {
  const classes = useStyles();

  // getModalStyle is not a pure function, we roll the style only on the first render
  const [modalStyle] = React.useState(getModalStyle);
  const [openSimpleModal, setSimpleModal] = React.useState(true);

  const [custType, setCustType] = React.useState(props.token);

  const handleSMClose = () => {
    setSimpleModal(false);
    props.closeParentModal(false);
    setCustType("");
  };



  return (
    <div>
      <Modal
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
        open={openSimpleModal}
        onClose={handleSMClose}
      >
        <div style={modalStyle} className={classes.paper}>
          <div>
            <h2 id="simple-modal-title">Token</h2>
            <div id="simple-modal-description">Customer type <span> <ArrowRightAltIcon color={"secondary"}/> </span>  <strong> {custType}</strong> </div>
          </div>
          <div>
            <div id="simple-modal-description">Token No <span> <ArrowRightAltIcon color={"secondary"}/> </span>  <strong>{props.count}</strong></div>
          </div>
          <div>
            <div id="simple-modal-description">
              Guest Count <span> <ArrowRightAltIcon color={"secondary"}/> </span> <strong> {props.guestCount.guestCount}</strong> 
            </div>
          </div>
        </div>
     

      </Modal>
    </div>
  );
}
