import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import ListItem from "@material-ui/core/ListItem";
import List from "@material-ui/core/List";
import InputAdornment from "@material-ui/core/InputAdornment";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import CloseIcon from "@material-ui/icons/Close";
import Slide from "@material-ui/core/Slide";
import TextField from "@material-ui/core/TextField";
import Container from "@material-ui/core/Container";
import CallIcon from "@material-ui/icons/Call";
import { useForm } from "react-hook-form";
import PeopleIcon from '@material-ui/icons/People';
import SimpleModalComponent from './helpers/SimpleModalComp';


const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function WaitingListComponent({onStarUpdate}) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  // const [guestName, guestEmail,guestDate] = React.useState('');

  const { handleSubmit, register, errors, reset } = useForm();

  const hasError = inputName => !!(errors && errors[inputName]);

  const [openSimple, setOpenSimple] = React.useState(false);

  const guestHandleClickOpen = () => {
    setOpen(true);
  };

  const guestHandleClose = () => {
    setOpen(false);
  };
  const onSubmit = values => {
   console.log(openSimple);
   // onStarUpdate(values);
   setOpenSimple(true);
    reset();
    // setOpen(false);
   
  };

  const closeParentModal = (flag) => {
    setOpen(flag);
    setOpenSimple(false);
  };
  return (
    <div className={classes.guestWrapper}>
     <Button variant="contained" className={classes.btn} color="primary"  onClick={guestHandleClickOpen}>
       Waiting list
      </Button>

      <Dialog
        fullScreen
        open={open}
        onClose={guestHandleClose}
        TransitionComponent={Transition}
      >
        <AppBar className={classes.appBar}>
          <Toolbar>
            <IconButton
              edge="start"
              color="inherit"
              onClick={guestHandleClose}
              aria-label="close"
            >
              <CloseIcon />
            </IconButton>
            <Typography variant="h6" className={classes.title}>
              Back
            </Typography>
         
          </Toolbar>
        </AppBar>

        <Container maxWidth="md" className="form-container">
          <form className={classes.root} onSubmit={handleSubmit(onSubmit)}>
            <List>
             
              
              <ListItem>
                <TextField
                  fullWidth
                  id="ContactNumber"
                  name="guestContact"
                  type='number'
                  inputRef={register({
                    required: "Required"
                  })}
                  error={hasError("guestContact")}
                  helperText="Contact number is mandatory"
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <CallIcon color="primary" />
                      </InputAdornment>
                    )
                  }}
                />
              </ListItem>
              <ListItem>
                <TextField
                  fullWidth
                  id="standard-basic"
                  name="guestCount"
                  inputRef={register({
                    required: "Required"
                  })}
                  error={hasError("guestCount")}
                  helperText="Guest count is mandatory"
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <PeopleIcon color="primary" />
                      </InputAdornment>
                    )
                  }}
                />
              </ListItem>
             
            </List>
            <Button
              variant="contained"
              color="secondary"
              type="submit"
              className={classes.cancelBtn}
              onClick={reset}
            >
              Cancel
            </Button>
            <Button
              variant="contained"
              color="secondary"
              type="submit"
              className={classes.savebtn}
            >
             Get Token
            </Button>
          </form>
        </Container>
      </Dialog>
    {openSimple &&  <SimpleModalComponent  closeParentModal = {closeParentModal }/> }
    </div>
  );
}


const useStyles = makeStyles(theme => ({
    appBar: {
      position: "relative",
      backgroundImage:
        " linear-gradient(to right, #4CB8C4 0%, #3CD3AD 51%, #4CB8C4 100%)"
    },
  
    title: {
      marginLeft: theme.spacing(2),
      flex: 1
    },
    cancelBtn: {
      width: "20%",
      marginTop: "20%"
    },
    addGuestBtn:{
        width: "18%"
    },
    savebtn: {
      float: "right",
      width: "20%",
      marginTop: "20%"
    },
    guestWrapper: {
      display: "inline"
    },

    btn: {
        margin: "0 auto",
        display: "block",
        width: "70%",
        backgroundImage:
          " linear-gradient(to right, #4CB8C4 0%, #3CD3AD 51%, #4CB8C4 100%)",
        fontWeight: 800,
        lineHeight: 3.75,
        color: "black",
        "&:hover": {
          backgroundPosition: "right center"
        }
    }

  }));