import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import ListItem from "@material-ui/core/ListItem";
import List from "@material-ui/core/List";
import InputAdornment from "@material-ui/core/InputAdornment";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import CloseIcon from "@material-ui/icons/Close";
import Slide from "@material-ui/core/Slide";
import TextField from "@material-ui/core/TextField";
import AccountCircleOutlinedIcon from "@material-ui/icons/AccountCircleOutlined";
import Container from "@material-ui/core/Container";
import CallIcon from "@material-ui/icons/Call";
import { useForm } from "react-hook-form";
import { Rating } from "@material-ui/lab";
import Box from "@material-ui/core/Box";
import NotificationBar from './NotificationBar';
import AjaxHelper from './helpers/AjaxHelper';
import GuestInputForm from './helpers/GuestInputForm'

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function NonRegisterCustomerIdiscont() {

  const { handleSubmit, register, errors, reset } = useForm();

  const [openSimple, setOpenSimple] = React.useState(false);
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  const [errorLabel, setErrorLabel] = React.useState(false);

  const [guestsList, setGuestList] = React.useState([]);

  const [guestConter, setGuestCOunter] = React.useState([]);

  const handleClickOpen = () => {
    setOpen(true);
  };
  const closeParentModal = (flag) => {
    setOpen(flag);
    setOpenSimple(false);
  };
  const guestList = {
    name:'',
    mobile:'',
    dob:'',
    doa:'',
    married:false,
    valid: false
  }
  const hasMainError = inputName => !!(errors && errors[inputName]);
  const handleClose = () => {
    setOpen(false);
    setGuestList([]);
    setGuestCOunter([]);
  };
  const updateGuList = (user) => {
    setGuestList(user);
  }
  const onSubmit = values => {
    // Calculate discount here
    
   AjaxHelper.createPost({
     API:'/save',
    data: guestsList,
    sucessCallback: function(res) {
      console.log('Saved successfully...');
      handleClose();
    },
    errorCallback: function(error) {
      console.log('error', error);
      handleClose();
    }

   })
   setOpenSimple(true);
   reset();   
  };
  const guestCountChange = (e)=> {
  
    if ( e.target.value > 10) {
          setErrorLabel(true);
          setGuestCOunter([]);
          return;   
    } else {
      setErrorLabel(false);
    }
   
    let tempArr = [];
    for (var i =0 ; i < e.target.value; i++ ) {
      tempArr.push(guestList)
    }
    setGuestCOunter(tempArr);
  }

  return (
    <div>
      <Button
        variant="contained"
        className={classes.btnExtraMargin}
        color="primary"
        onClick={handleClickOpen}
      >
        Non Register customers interested for discount
      </Button>

      <Dialog
        fullScreen
        open={open}
        onClose={handleClose}
        TransitionComponent={Transition}
      >
        <form className={classes.root} onSubmit={handleSubmit(onSubmit)}>
          <AppBar className={classes.appBar}>
            <Toolbar>
              <IconButton
                edge="start"
                color="inherit"
                onClick={handleClose}
                aria-label="close"
              >
                <CloseIcon />
              </IconButton>
              <Typography variant="h6" className={classes.title}>
                Cancel
              </Typography>
              <Button autoFocus color="inherit" type="submit">
                Save
              </Button>
            </Toolbar>
          </AppBar>

          <Container maxWidth="lg" className="form-container">
            <List>
              <ListItem>
                <TextField
                  fullWidth
                  id="ContactNumber"
                  name="customerNumber"
                  inputRef={register({
                    required: "Required"
                  })}
                  error={hasMainError("customerNumber")}
                  helperText="Enter your mobiles"
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <CallIcon color="primary" />
                      </InputAdornment>
                    )
                  }}
                />
              </ListItem>

              <ListItem>
                <TextField
                  fullWidth
                  id="standard-basic"
                  name="guestCount"
                  onChange={guestCountChange}
                  inputRef={register({
                    required: "Required"
                  })}
                  error={hasMainError("guestCount")}
                  helperText="Enter guest count"
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <AccountCircleOutlinedIcon color="primary" />
                      </InputAdornment>
                    )
                  }}
                />
              </ListItem>
              <ListItem> 
                { errorLabel ?  <label className="error-label"> For more than 10 guests, please collect form from reception.</label>
          :'' }
                    </ListItem>
            </List>

    <GuestInputForm count = {guestConter}  udpateGList={updateGuList}/>

            <Button
              variant="contained"
              color="secondary"
              type="submit"
              className={classes.cancelBtn}
              onClick={handleClose}
            >
              Cancel
            </Button>
            <Button
              variant="contained"
              color="secondary"
              type="submit"
              className={classes.savebtn}
            >
              Save
            </Button>

          </Container>
        </form>
      </Dialog>
    </div>
  );
}

  

function GuestAddedComponent(guestsList) {
    let notifiactionArea;

    if (guestsList.guestListData.length) notifiactionArea =   <NotificationBar guestCount = {true} />;

  return (
    <div>
      <Box component="fieldset" mb={3} borderColor="transparent">
        <Typography component="legend">Discount earned </Typography>
        <Rating
          name="read-only"
          value={guestsList.guestListData.length}
          readOnly
        />
      </Box>
      {notifiactionArea}
    </div>
  );
}
