import axios from 'axios';

export default {
  
     createPost:function(opts) {
       let SERVER_URL = 'https://shreeserver.herokuapp.com'
            console.log('Posting request to Server...', opts);
        let data = JSON.stringify(opts.data);
        let  payload = {data}
           
            axios({
                method: 'post',
                url: SERVER_URL + opts.API,
                data: payload
                })

              .then(function (response) {
               debugger
               opts.sucessCallback(response)
              })
              .catch(function (error) {
                debugger
               opts.ErrorCallback(error)
              });
      }
    }