import React from "react";
import TextField from "@material-ui/core/TextField";
import { withStyles } from "@material-ui/core/styles";
import { green } from "@material-ui/core/colors";
import { makeStyles } from "@material-ui/core/styles";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import AjaxHelper from "./AjaxHelper";
import CheckCircleOutlineIcon from "@material-ui/icons/CheckCircleOutline";
import BlockIcon from '@material-ui/icons/Block';

export default function GuestInputForm(props) {
  const classes = useStyles();
  const [state, setState] = React.useState(props.count);

  React.useEffect(() => {
    // way to imporove
    let newObj = JSON.stringify(props);
    let tempObj = JSON.parse(newObj);
    setState(tempObj.count);
  }, [props.count]);

  const handleInputChange = (index, event) => {
    let newArr = [...state];
    if (event.target.name === "name") {
      newArr[index].name = event.target.value;
    } else if (event.target.name === "mobile") {

      newArr[index].mobile = event.target.value;
    } else if (event.target.name === "dob") {
      newArr[index].dob = event.target.value;
    }
    if (event.target.name === "doa") {
      newArr[index].doa = event.target.value;
    } else if (event.target.name === "married") {
      newArr[index].married = event.target.checked;
    }

    validateData(index);
    setState(newArr);
  };

  const validateData = async (index) => {
    let newArr = [...state];
    console.log('validating', index);
    let userObject = newArr[index];
    if (userObject.married) {
      if (
        userObject.name !== "" &&
        userObject.dob !== "" &&
        userObject.mobile !== "" && userObject.mobile.length == "10" 
      ) {
        userObject.valid = await  _checkDuplicateMobile(userObject.mobile);
      } else {
        userObject.valid = false;
      }
    } else {
      if (
        userObject.name !== "" &&
        userObject.dob !== "" &&
        userObject.doa !== "" &&
        userObject.mobile !== "" && userObject.mobile.length == "10" 
      ) {
        userObject.valid = await _checkDuplicateMobile(userObject.mobile);
      } else {
        userObject.valid = false;
      }
    }
    props.udpateGList(state);
  };




  const _checkDuplicateMobile = mobile => new Promise((resolve, reject) => {  
    AjaxHelper.createPost({
      API: "/validate",
      data: mobile,
      sucessCallback: function(res) {
       let flag = false;
         if (res.data.found == 0) {
          flag = false;
         } else {
            flag = true;
         }
  //       return flag;
         resolve(flag)
      },
      errorCallback: function(error) {
        console.log("error", error);
        reject(error);
      }
    });
  });

  return (
    <div className={classes.root}>
      <div className="discount-form">
        {state.map((person, index) => (
          <div key={`${person}~${index}`} className="discount-row">
            <TextField
              label="Guest Name"
              variant="outlined"
              name="name"
              value={state.name}
              onChange={event => handleInputChange(index, event)}
            />
            <TextField
              label="mobile number"
              variant="outlined"
              name="mobile"
              type="number"
              value={state.mobile}
              onChange={event => handleInputChange(index, event)}
               onInput={(e)=>{ 
                   e.target.value = Math.max(0, parseInt(e.target.value) ).toString().slice(0,10)
                }}
                error={person.valid}
            />
            <TextField
              type="date"
              variant="outlined"
              name="dob"
              value={state.dob}
              onChange={event => handleInputChange(index, event)}
            />
            <TextField
              name="doa"
              value={state.doa}
              type="date"
              variant="outlined"
              onChange={event => handleInputChange(index, event)}
              disabled={person.married}
            />
            <FormControlLabel
              control={
                <GreenCheckbox
                  onChange={event => handleInputChange(index, event)}
                  value={state.married}
                  name="married"
                />
              }
              label="Unmarried"
            />
            <span className={classes.topPadding}>
              {person.valid ? (
                <label>
                  <BlockIcon color={"error"} />
                </label>
              ) : (
               "" 
                // <CheckCircleOutlineIcon color={"primary"} />
              )}
            </span>
          </div>
        ))}
      </div>
    </div>
  );
}

function SuccessCallBack(res) {
  console.log("Sucess", res);
}
function ErrorCallback(error) {
  console.log("error", error);
}
const useStyles = makeStyles(theme => ({
  root: {
    "& .MuiTextField-root": {
      margin: theme.spacing(1),
      width: 160
    }
  },

  topPadding: {
    marginTop: "22px"
  },
  LabelTopPadding: {
    paddingTop: "26px"
  },
  formContainer: {
    display: "flex"
  }
}));

const GreenCheckbox = withStyles({
  root: {
    color: green[400],
    "&$checked": {
      color: green[600]
    }
  },
  checked: {}
})(props => <Checkbox color="default" {...props} />);
